.top {
  background: url('../img/bg.jpg') no-repeat;
  background-position: top center;
  background-size: cover;

  &::before {
    @include pseudo($content: '', $display: block, $pos: absolute);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(45.97% 54.43% at 50% 45.57%, rgba(23, 26, 28, 0) 50.28%, rgba(23, 26, 28, 0.00225974) 50.29%, rgba(4, 5, 5, 0.820343) 58.72%, rgba(5, 5, 6, 0.792703) 80.08%, #000000 87.89%);
    opacity: 0.4;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
  }

  &__title {
    margin-bottom: 2.5rem;

    @include font-lite($f96, 11.5rem);
    @extend %sf-semibold;
  }

  &__text {
    margin: 2rem 0 3.5rem;
    max-width: 32.5rem;
  }

  &__social-list {
    display: flex;
  }

  &__social-item {
    display: inline-block;
    margin: 0 2.5rem;
  }
}

.social__icon {
  width: 4rem;
  height: 4rem;

  fill: $white;
  opacity: .5;

  transition: all .3s;

  &:hover {
    opacity: 1;
  }
}