body {
  @extend %ubuntu-light;
  color: $white;
  font-size: 1.7rem;
  line-height: 2.5rem;
}

.none {
  display: none;
}

.container {
  max-width: 127rem;
  padding: 0 1.5rem;
  margin: 0 auto;
}

.page-section {
  background: url("../img/bg-2.jpg") no-repeat;
  background-position: top center;
  background-size: cover;

  position: relative;

  &::before {
    @include pseudo($content: '', $display: block, $pos: absolute);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(107.54deg, rgba(23, 26, 28, 0.72) 0%, #5E5E57 100%);
    opacity: 0.15;
  }

  &__wrapper {
    // display: flex;
    // align-items: center;
    // align-items: flex-end;
    padding: 7.4rem 0 5rem;
    min-height: 100vh;


    position: relative;
  }
}

.title {
  @extend %sf-regular;
  @include font-lite($f44, $lh53);
  padding-top: 20rem;
  padding-left: 2.5rem;
  margin-bottom: 5.5rem;
}

.scroll__btn {
  padding: 1.3rem 0 3rem;

  text-align: center;

  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.arrow {
  &-down {
    fill: transparent;
    width: 1.5rem;
    height: 0.7rem;
  }

  &-up {
    transform: rotate(180deg);
  }
}