.header {
  position: fixed;
  left: 0;
  top: 3rem;
  right: 0;

  z-index: 10;


  &__inner {
    @include flex($sb);
    align-items: center;
    min-height: 3rem;

    @include font-lite($f15, 1.7rem);
    @extend %ubuntu-medium;
    text-transform: uppercase;
  }

  &__btn {
    padding: 0.6rem 2.8rem;
    margin-right: .8rem;
    width: 11.5rem;
    height: 3rem;

    @include font-lite($f15, 1.7rem);
    @extend %ubuntu-medium;
    color: $white;
    text-transform: uppercase;

    background: transparent;
    border: 1px solid $white;
    border-radius: .3rem;

    transition: all .3s;

    &:hover {
      color: #000;
      background: $white;
    }
  }

  &__phone {
    @include font-lite($f16, 1.8rem);
    @extend %ubuntu-bold;

  }
}

.menu {

  &__list-item {
    display: inline-block;

    &+.menu__list-item {
      padding-left: 2rem;
    }
  }

}