.specification {
  &__inner {
    @include flex($sb);
    align-items: center;
    padding-left: 2.5rem;
  }

  &__item {
    &--center {
      margin-left: -1.5rem;
      margin-right: -1.5rem;
    }
  }

  &__box {
    margin-top: 1rem;
    width: 28rem;

    position: relative;

    &::before {
      @extend %pseudo;
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: cover;
    }

    &-title {
      margin-bottom: 1rem;

      @extend %ubuntu-bold;
      @include font-lite($f21, $lh28);
    }

    &-text {
      @extend %ubuntu-light;
      @include font-lite($f15, $lh19);

    }

    &-2 {
      margin-top: 14rem;
    }

    &-4 {
      margin-top: 16.6rem;
    }

    &-1::before {
      width: 22.9rem;
      height: 11.5rem;

      background-image: url('../img/svg/pointer-1.svg');

      top: 1.8rem;
      right: -15rem;
    }

    &-2::before {
      width: 34.6rem;
      height: 8.1rem;

      background-image: url('../img/svg/pointer-2.svg');

      top: -6.1rem;
      right: -30rem;
    }

    &-3::before {
      width: 25.8rem;
      height: 10.4rem;

      background-image: url('../img/svg/pointer-3.svg');

      top: 1.6rem;
      left: -27rem;
    }

    &-4::before {
      width: 12.4rem;
      height: 8.9rem;

      background-image: url('../img/svg/pointer-4.svg');

      top: -6.7rem;
      left: -13rem;
    }
  }
}