.benefits {
  z-index: 1;

  &__items {
    @include flex($sb);
    flex-wrap: wrap;

    padding-left: 3.2rem;

  }

  &__item {
    display: flex;
    align-items: flex-end;
    margin-bottom: 10rem;

    &:nth-child(even) {
      margin-left: 4rem;
    }

    &:nth-child(3) {
      margin-left: 3rem;
    }

    &:nth-child(4) {
      margin-left: 6.3rem;
      transform: translateY(2.5rem);
    }

    &:first-child,
    &:nth-child(2),
    &:nth-child(3) {
      position: relative;

      &::before {
        @include pseudo($content: '', $display: block, $pos: absolute);
        background-position: 0 0;

      }
    }

    &:first-child {
      &::before {
        width: 24.5rem;
        height: 5.5rem;

        background: url('../img/svg/line 1-2.svg') no-repeat;

        right: -4.7rem;
        bottom: 4.8rem;
      }
    }

    &:nth-child(2) {
      transform: translate(-6.6rem, 2.5rem);

      &::before {
        width: 46.3rem;
        height: 6.3rem;

        background: url('../img/svg/line 2-3.svg') no-repeat;

        left: -44.8rem;
        bottom: -6.5rem;
      }
    }

    &:nth-child(3) {

      &::before {
        width: 25.4rem;
        height: 4.9rem;

        background: url('../img/svg/line 3-4.svg') no-repeat;

        right: -8rem;
        top: 4rem;
      }
    }
  }

  &__num {
    margin-right: 1rem;

    @extend %ubuntu-bold;
    @include font-lite($f36, $lh41);
  }

  &__img {
    width: 19.5rem;
    height: 13rem;
    margin-right: 3.2rem;

    position: relative;

    &::before {
      @include pseudo($content: '', $display: block, $pos: absolute);
      width: 19.5rem;
      height: 13rem;

      border: .2rem solid $white;

      top: -1rem;
      right: -1.2rem;
      z-index: -1;
    }
  }

  &__info {
    width: 30rem;

    &-title {
      margin-bottom: 1rem;

      @extend %ubuntu-bold;
      @include font-lite($f21, $lh24);
    }

    &-text {
      // width: 30rem;

      // @extend %ubuntu-bold;
      @include font-lite($f15, $lh19);
    }
  }
}