.contacts {

  &__inner {
    // @include flex($sb);
    display: flex;
    // align-items: center;
    padding-left: 2.5rem;
  }

  &__info {
    width: 24rem;
    margin-right: 6rem;

    &-box {
      margin-bottom: 3rem;
    }

    &-title {
      margin-bottom: 1.3rem;

      @include font($f21, $lh24, 700, $white);
    }

    &-phone,
    &-email {
      @include font-lite($f15, $lh19);
    }

    &-email {
      @include font-lite($f15, $lh19);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__form {
    width: 57rem;
    margin-right: 10.6rem;

    &-title {
      margin-bottom: 1.3rem;

      @include font($f21, $lh24, 700, $white);
    }

    &-text {
      margin: 0 0 1.2rem;

      @include font-lite($f15, $lh19);
    }

    &-line {
      @extend %sb;
    }

    &-input,
    &-area {
      padding: 1.5rem;

      @extend %ubuntu-regular;
      @include font($f13, $lh17, 400, $white);

      background: rgba(0, 0, 0, 0.5);

      &::placeholder {
        color: rgba($white, .4);
      }
    }

    &-input {
      width: 49%;
      // height: 4.6rem;
      margin-bottom: 1rem;
    }

    &-area {
      width: 100%;
      min-height: 14.4rem;
      margin-bottom: 2rem;

      resize: none;
    }

    &-btn {
      padding: 0.9rem 1.5rem;

      @extend %ubuntu-bold;
      @include font($f14, $lh16, 700, $white);
      text-transform: uppercase;

      background: transparent;

      border: 1px solid #FFFFFF;
      border-radius: 2px;
      transition: all .3s;

      &:hover {
        color: #000;
        background: $white;
      }
    }
  }

  &__social-list {
    @include flex($ce);
    flex-direction: column;
  }

  &__social-item {
    // display: inline-block; 
    // margin-bottom: 2.8rem;
    margin: 1.4rem 0;
  }

}

.footer {
  position: absolute;
  bottom: 3.2rem;
  left: 0;
  right: 0;

  &__copy {
    @include font-lite($f13, $lh17);
    text-align: right;
    color: rgba($white, .64);
    // opacity: 0.64;
  }
}