.questions {

  &__items {
    max-width: 75.7rem;
    padding-bottom: 1.5rem;
  }

  &__item {
    max-width: 69rem;
    margin-bottom: 0.5rem;

    background: rgba(0, 0, 0, 0.3);

    position: relative;

    &:nth-child(3)::before {
      @extend %pseudo;

      width: 31.9rem;
      height: 12.5rem;

      background: url('../img/drone-questions.png');

      top: -3.6rem;
      right: -39rem;
    }
  }

  &__item-title {
    padding: 2rem 1rem 2.4rem 4rem;

    color: rgba($white, .8);
    @extend %ubuntu-medium;
    @include font-lite($f21, $lh28);

    position: relative;

    cursor: pointer;

    &::after {
      @include flex($ce);
      align-items: center;

      content: url(../img/svg/arrow-question_open.svg);

      width: 7.2rem;
      height: 6.7rem;
      background-color: $white;

      position: absolute;
      top: 0;
      right: -7.2rem;
    }
  }

  &__item-text {
    // padding: 0 0.5rem 7.5rem 5rem;
    padding: 0 0.5rem 0 5rem;

    @include font-lite($f15, $lh22);

    display: none;
  }

  &__item.questions__item--active {
    .questions__item-title::after {
      transform: rotate(180deg);
    }

    .questions__item-text {
      padding: 0 0.5rem 7.5rem 5rem;

      display: block;
    }
  }

}