.products {
  &__inner {
    @include flex($sb);
    padding-left: 5.5rem;
    margin-bottom: 10rem;
  }

  &__text {
    max-width: 44rem;
    margin-right: 13.6rem;

    &-paragraph {
      margin-bottom: 3rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__slider-box {
    padding-top: 11rem;
    // margin-left: 13.6rem;

    position: relative;
  }

  &__slider-text {
    max-width: 28.1rem;

    @extend %ubuntu-medium;
    @include font-lite($f24, $lh28);
    text-align: right;

    position: absolute;
    left: -23.5rem;
    bottom: 3rem;
    z-index: 5;
  }

  &__slider {
    max-width: 56rem;

    &::before {
      @include pseudo($content: '', $display: block, $pos: absolute);
      width: 56.5rem;
      height: 28rem;

      border: .4rem solid $white;

      top: -1.5rem;
      right: -1.7rem;

    }

    &-img {
      width: 100%;
      height: 30rem;
      // padding: 0 1rem;

      object-fit: cover;
    }
  }
}

.slider-btn {
  padding: 1.6rem 2.6rem;
  // width: 6rem;
  // height: 4.8rem;

  background-color: $white;

  position: absolute;
  bottom: -4.8rem;
  right: 0;
  z-index: 5;

  transition: all .3s linear;

  &__left {
    right: 6rem;
  }

  &.slick-disabled {
    background-color: $black;

    .slider-arrow {
      stroke: $white;
    }
  }
}


.slider-arrow {
  width: 1rem;
  height: 1.8rem;

  stroke: $black;
}