// ШРИФТЫ
$roboto:'Roboto Condensed',
sans-serif;
$pop:'Poppins',
sans-serif;

%ubuntu-light {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

%ubuntu-regular {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 400;
}

%ubuntu-medium {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 500;
}

%ubuntu-bold {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 700;
}

%sf-regular {
    font-family: 'SFProDisplay-Regular', sans-serif;
    font-weight: 400;
}

%sf-semibold {
    font-family: 'SFProDisplay-Semibold', sans-serif;
    font-weight: 600;
}

// var flex
$sb: space-between;
$sa: space-around;
$se: space-evenly;
$ce: center;

// projects color
$white:#FFF;
$black:#121212;

// font-size
$f12:1.2rem;
$f13:1.3rem;
$f14:1.4rem;
$f15:1.5rem;
$f16:1.6rem;
$f18:1.8rem;
$f20:2rem;
$f21:2.1rem;
$f22:2.2rem;
$f24:2.4rem;
$f26:2.6rem;
$f28:2.8rem;
$f30:3rem;
$f34:3.4rem;
$f36:3.6rem;
$f38:3.8rem;
$f40:4rem;
$f44:4.4rem;
$f48:4.8rem;
$f96:9.6rem;

// line-height
$lh12:1.2rem;
$lh14:1.4rem;
$lh16:1.6rem;
$lh17:1.7rem;
$lh18:1.8rem;
$lh19:1.9rem;
$lh20:2rem;
$lh22:2.2rem;
$lh24:2.4rem;
$lh26:2.6rem;
$lh28:2.8rem;
$lh30:3rem;
$lh33:3.3rem;
$lh34:3.4rem;
$lh35:3.5rem;
$lh36:3.6rem;
$lh41:4.1rem;
$lh44:4.4rem;
$lh51:5.1rem;
$lh52:5.2rem;
$lh53:5.3rem;
$lh54:5.4rem;

// mixins
@mixin font($size, $lh, $weight, $color) {
    font-size: $size;
    line-height: $lh;
    font-weight: $weight;
    color: $color;
}

@mixin font-lite($size, $lh) {
    font-size: $size;
    line-height: $lh;
}

@mixin flex($sb) {
    display: flex;
    justify-content: $sb;
}


@mixin pseudo($content: '', $display: block, $pos: absolute) {
    content: $content;
    display: $display;
    position: $pos;
}

%pseudo {
    content: '';
    display: block;
    position: absolute;
}

// ШАБЛОНЫ @extend
%sb {
    display: flex;
    justify-content: space-between;
}